<template>
  <div class="container-wrap">
    <div class="container-box" v-loading="is_loading" element-loading-text="拼命加载中~~~">
      <div class="">

      </div>
      <div class="search-box">
        <el-input placeholder="请输入关键字" icon="search" v-model="key_word" :on-icon-click="handleIconClick">
        </el-input>
      </div>
      <div class="container-content">
        <el-table :data="accessLogs" border stripe tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="createTime" label="操作时间" width="180" align="center">
            <template scope="scope">
              {{scope.row.createTime | date}}
            </template>
          </el-table-column>
          <el-table-column prop="createBy" label="用户id" width="200" align="center"></el-table-column>
          <el-table-column prop="createByDesc" label="用户名" width="200" show-overflow-tooltip align="center"></el-table-column>
          <el-table-column prop="userIp" label="访问IP" width="180" align="center"></el-table-column>
          <el-table-column prop="phone" label="手机型号" width="250" align="center"></el-table-column>
          <el-table-column prop="appVersion"  label="APP版本" width="180" show-overflow-tooltip align="center"></el-table-column>
          <el-table-column prop="operationDesc" label="事件" width="180" align="center"></el-table-column>
          <el-table-column prop="note" label="事件内容" align="center"></el-table-column>
        </el-table>
      </div>
      <div class="page-wrap">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
  import LogService from '../../service/log';
  import types from '../../store/mutation-types';
  export default {

    data() {
      return {
        currentPage: 1,
        totalPage: 0,
        pageSize: 18,
        totalCount: 0,
        key_word: '',
        is_loading: false,
        accessLogs: []
      }
    },
    created () {
      this.logsData();
    },
    watch: {
      '$route': 'logsData'
    },
    computed: {
      keyWordUsers () {
        var arr = [];
        if(!this.key_word)
          return this.accessLogs;
        else{
          this.accessLogs.forEach((item,index) => {
            if(item.username.indexOf(this.key_word) > -1)
              arr.push(item);
          });
          return arr;
        }
      }
    },
    methods: {

      handleIconClick () {
        if(!this.key_word){
          this.$message({type: 'info', message: '请输入关键字'});
          return;
        }
        this.logsData();
      },
      handleCurrentChange (val) {
        this.currentPage = val;
        this.logsData();
      },
      /**获取访问日志列表数据*/
      logsData (route) {
          let that = this;
        that.is_loading = true;
        setTimeout( () => {
          LogService.getAccessLogs({
            page: that.currentPage-1,
            length: that.pageSize,
            filter: that.key_word
          }).then(function (response) {
            if(response.data.code == 0) {
              that.accessLogs = response.data.data.rows;
              that.totalPage = response.data.totalPage;
              that.totalCount = response.data.data.totalCount;
            }
            else that.$message({type: 'error', message: response.data.reason});
            that.is_loading = false;
          });
        },300);
      }
    }
  }



</script>
<style lang="scss">
  @import "../../assets/scss/define";
  .container-content{
  @extend %pa;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 70px 20px;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .search-box{
  @extend %pa;
    top: 20px;
    right: 40px;
    z-index: 1;
    width: 360px;
  }
  .container-box{
  @extend %h100;
  }
  .page-wrap{
  @extend %pa;
  @extend %tac;
    background-color: #fff;
    z-index: 1;
    left: 0;
    right: 0;
    padding: 10px 0 20px;
    bottom: 0;
  }
</style>



