import axios from 'axios';

export default{
  getList(data){
    return axios.get('/users',{params:data});
  },
  get(data){
    return axios.get('/users/'+data);
  },
  update(data){
    return axios.post("/users",data);
  },
  getFriends(data){
    return axios.get("/users/friends",{params:data});
  },
  resetPassword(data){
    return axios.post("/users/reset",data);
  }
}



